<style lang="css">
.card__category {
  height: 250px;
  background: #000;
  position: relative;
}
.card__category img {
  max-width: 100%;
}
.card__category::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(63, 81, 181, .7);
  z-index: 333;
}
#projects {
  background: url('../../assets/projects.jpg');
  background-size: cover;
}
#infond {
  background: url('../../assets/inf.jpg');
  background-size: cover;
}
#investors {
  background: url('../../assets/investor.jpg');
  background-size: cover;
}
#volonters {
  background: url('../../assets/volonter.jpg');
  background-size: cover;
}
.title__before {
  z-index: 444;
  text-align: center;
  color: #fff;
  margin-top: 30%;
  font-weight: bold;
  position: relative;
}
.title__before::before {
  content: '';
  width: 0;
  height: 2px;
  position: absolute;
  display: block;
  top: 40px;
  background-color: #fff;
  transition: all .6s ease;
  left: 0;
}
.title__before:hover::before {
  width: 100%
}
</style>



