<template lang="html">
  <div class="container-fluid --container-link">
    <ul class="row list-link">
      <li key="nav-l1" class="link-item lead mr-3 ml-3"><a href="#projectsS">Projects</a></li>
      <li key="nav-l2" class="link-item lead mr-3 ml-3"><a href="#">Investment fund</a></li>
      <li key="nav-l3" class="link-item lead mr-3 ml-3"><a href="#investorsS">Investors</a></li>
      <li key="nav-l4" class="link-item lead mr-3 ml-3"><a href="#">Volunteers</a></li>
      <li key="nav-l5" class="link-item lead mr-3 ml-3"><a href="#">News</a></li>
      <btn key="nav-l6" color="outline-indigo" class="ml-5" @click.native="modal = true">Log in</btn>
        <a href="/" class="btn btn-outline-indigo">RU</a>


    </ul>
    <modal v-if="modal" @close="modal = false">
      <modal-header>
        <modal-title>Modal title</modal-title>
      </modal-header>
      <modal-body>
        <!-- Default form login -->
        <form v-if="!isLogin">
          <p class="h4 text-center mb-4">Sign in</p>
          <label for="defaultFormLoginEmailEx" class="grey-text">Your email</label>
          <input type="email" id="defaultFormLoginEmailEx" class="form-control"/>
          <br/>
          <label for="defaultFormLoginPasswordEx" class="grey-text">Your password</label>
          <input type="password" id="defaultFormLoginPasswordEx" class="form-control"/>
          <div class="text-center mt-4">
            <button class="btn btn-indigo" type="submit">Login</button>
          </div>
        </form>
        <!-- Default form login -->
              <!-- Default form register -->
        <form v-if="isLogin">
          <p class="h4 text-center mb-4">Sign up</p>
          <label for="defaultFormRegisterNameEx" class="grey-text">Your name</label>
          <input type="text" id="defaultFormRegisterNameEx" class="form-control"/>
          <br/>
          <label for="defaultFormRegisterEmailEx" class="grey-text">Your email</label>
          <input type="email" id="defaultFormRegisterEmailEx" class="form-control"/>
          <br/>
          <label for="defaultFormRegisterConfirmEx" class="grey-text">Confirm your email</label>
          <input type="email" id="defaultFormRegisterConfirmEx" class="form-control"/>
          <br/>
          <label for="defaultFormRegisterPasswordEx" class="grey-text">Your password</label>
          <input type="password" id="defaultFormRegisterPasswordEx" class="form-control"/>
          <div class="text-center mt-4">
            <btn color="indigo" >Sign up</btn>
          </div>
        </form>
        <!-- Default form register -->
      </modal-body>
      <modal-footer v-if="!isLogin">
        not account?
        <btn color="outline-indigo" @click.native="isLogin = true">Sign up</btn>
      </modal-footer>
      <modal-footer v-if="isLogin">
        have account?
        <btn color="outline-indigo" @click.native="isLogin = false">Log in</btn>
      </modal-footer>
    </modal>
  </div>
</template>

<script>
import { Modal, ModalHeader, ModalTitle, ModalBody, ModalFooter, Btn, Form } from 'mdbvue';
export default {
  name: 'navbar',
  components: {
    Modal,
    ModalHeader,
    ModalTitle,
    ModalBody,
    ModalFooter,
    Btn
  },
  data() {
    return {
      modal: false,
      isLogin: false,
      lang: ''
    }
  },
  computed: {
    language() {
      var language = localStorage.getItem('lang');
      console.log(language)
      if (language === 'ru') {
        this.lang = "RU";

      } else {
        this.lang = "EN"
      }
    }
  }
}
</script>

<style lang="css">
.list-link {
  list-style-type: none;
  justify-content: center;
  align-items: center;
}
.--container-link {
  padding-top: 40px;
  padding-bottom: 40px;
}
.--container-link a {
  color: #2c3e50;
}
.link-item {
  position: relative;
}
.link-item::before {
  content: '';
  width: 30px;
  height: 2px;
  position: absolute;
  display: block;
  top: 100%;
  background-color: #3f51b5;
  transition: all .6s ease;
}
.link-item:hover::before {
  width: 100%
}
</style>



