      <ul class="docs__list" style="list-style-type:none;">
        <li>1. Whitepaper</li>
        <li>2. Onepager</li>
        <li>3. Pitch deck</li>
        <li>4. Business plan</li>
      </ul>
      <button type="button" class="btn btn-outline-indigo" name="button" id="btn">Add project</button>
      <label class="red-text" for="btn">To add a project you need to register</label>
      <h3 class="ml-3 mb-2 mt-5">Investor search for presale</h3>
      <p class="lead ml-3">Use the main and additional services of the Business Platform resource:</p>
      <ul>
        <li>- Preparation of the necessary documents</li>
        <li>- Search for advisors</li>
        <li>- Express analysis of the project and expertise</li>
        <li>- Priority accommodation</li>
        <li>- E-mailing on the basis of investors</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Title from './Title'
export default {
  name: 'Iniciators',
  components: {
    Title
  },
}
</script>

<style lang="css">
.iniciators__container{
  padding-top: 60px;
}
.card__features{
  width: 100%;
  margin: 0 auto;
}

.fix__block {
height: 52px;
}
.docs__list {
  margin-top: 20px;
}
</style>



